import { Container, ListItem, ListItemText, Typography, List, Button } from "@material-ui/core";
import { format } from "date-fns";
import { Link } from "react-router-dom";

function Highscores (props) {
    return(
        <Container maxWidth="sm">
            <Typography 
                variant="h3" 
                align="center" 
                style={{margin:15}}
            >
                Highscores
            </Typography>

            <List>
            {props.pisteTiedot.sort((a,b) => b.pisteet - a.pisteet).map((pistetieto, idx) => {
                return(
                    <ListItem key={idx} divider style={{width:520, textAlign: "center"}}>
                        <ListItemText primary={format(pistetieto.pvm, "d/M/y")} style={{width:200}}/>
                        <ListItemText primary={pistetieto.nimi} style={{width:200}}/>
                        <ListItemText primary={pistetieto.pisteet} style={{width:200}}/>
                    </ListItem>
                );
            })}
            </List>
            <Button
                component={Link}
                to="/peli"
                variant="outlined"
                style={{
                    backgroundColor: "#093009", 
                    color:"white",
                    marginTop:10,
                    marginLeft: 210
                }}
            >
                Try Again
            </Button>
        </Container>
    );
}

export default Highscores;