import { Container, Grid, Typography, Button } from "@material-ui/core";
import { Link } from "react-router-dom";

function Listaus(props) {

   
    return(

        <Container maxWidth="md">

            <Typography variant="h3" align="center" style={{marginTop:20}}></Typography>

            <Grid container justify="center">
                {props.data.henkilot.map((henkilo, char_id) => {
                    return(
                        <Grid item xs={4} key={char_id}>
                            <Button 
                                style={{
                                    width:250, 
                                    height:300, 
                                    margin:25}} 
                                component={Link} 
                                to={`/henkilotiedot/${henkilo.char_id}`}
                            >
                                <img src={henkilo.img} style={{height:250}} alt="Kuva"/>
                            </Button>
                        </Grid>
                    );
                })}
                        
            
                
            </Grid>
        </Container>
    );
    
}

export default Listaus;