import { Button, Container, FormControl, InputLabel, MenuItem, Select, Typography } from "@material-ui/core";
import { useState } from "react";
import { useHistory } from "react-router";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
    nappi: {
        marginTop: 50,
        marginLeft: 200,
        backgroundColor: "#093009",
        color: "white",
    },
    
});

function Peli (props) {

    const [henkilo1, setHenkilo1] = useState({});
    const [arvaus, setArvaus] = useState("");
    const [oikeatVastaukset, setOikeatvastaukset] = useState(0);
    const history = useHistory();
    const [aloita, setAloita] = useState(false);
    const tyylit = useStyles();
    const [oikein, setOikein] = useState("");

    const arvoHenkilo = () => {
        let henkilo1 = props.data.henkilot[Math.floor(Math.random()*props.data.henkilot.length)];
        
        setHenkilo1(henkilo1);
        setAloita(true);
    }

    const tarkista = () => {
        if (arvaus === henkilo1.name) {
            let laskuri = oikeatVastaukset;
            laskuri++;
            setOikeatvastaukset(laskuri);
            setOikein("CORRECT!")
            arvoHenkilo();
        } else {
            history.push({pathname:"/tallenna", state:{details: oikeatVastaukset}})
            setOikeatvastaukset(0);
        }
    }

    return(
        (aloita)
        ?
        <Container maxWidth="sm">
            <Typography 
                variant="h4" 
                align="center" 
                style={{
                    margin:20,
                    color:"#093009"}}
            >
                WHO?
            </Typography>

            <img src={henkilo1.img} alt="henkilo1" style={{height:400}}/>
                
                <FormControl 
                    style={{ 
                        marginTop:15, 
                        marginBottom: 15, 
                        }}
                >

                    <InputLabel style={{marginLeft:10}}>Select</InputLabel>

                    <Select
                        onChange={(e) => {setArvaus(e.target.value)}}
                        value={arvaus}
                        style={{width:300, color:"white"}}
                        variant="outlined">
                            {props.data.henkilot.map((henkilo,name) => {
                                return(
                                    <MenuItem 
                                        key={name} 
                                        value={henkilo.name} 
                                        style={{
                                            backgroundColor:"#093009", 
                                            color:"white"}}
                                        >
                                        {henkilo.name}
                                    </MenuItem>
                                );
                            })}
                    </Select>
                </FormControl>

                <Typography 
                    variant="h6"
                    style={{color:"#093009"}}
                >
                    Score: {oikeatVastaukset}
                </Typography>
                <Typography 
                    variant="h4"
                    style={{color:"#29773E"}}
                >
                    {oikein}
                </Typography>
                       
                <Button 
                    onClick={tarkista} 
                    variant="outlined" 
                    size="large" 
                    fullWidth
                    style={{
                        marginTop: 15,
                        backgroundColor:"#093009", 
                        color:"white",
                        }}
                    >
                        Check
                </Button>

        </Container>
        :

            <Container maxWidth="sm">
                
                <Button
                    onClick={arvoHenkilo} 
                    variant="outlined"
                    size="large"
                    className={tyylit.nappi}
                >
                    START GAME
                </Button>
                    
                    
            
            </Container>
        );
}

export default Peli;